body {
  margin: 0;
  padding: 0;  
  height: 100vh;
}

h1 {
	text-align: center;
}

h4 {
	padding: 0;
	margin: 0;
}

.background {
	background-color: #FFF6F6;
	border: 2px solid #D3C6C6;
	border-radius: 10px;
	padding: 5px;
	margin: 5px;
}

.colorBox {
	width: 120px;
	height: 20px;
	border: 2px solid #D3C6C6;
	border-radius: 10px;
	padding: 5px;
	margin: 5px;
}

#controlbar {
	opacity: 0.3;
}

